.loader {
  margin: 50px auto 0;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 2px dashed teal;
  animation: rotate 1s infinite linear;
}

@keyframes rotate {
  from {
    transform: rotate(0deg) scale(0.8);
  }
  to {
    transform: rotate(360deg) scale(1.2);
  }
}