.myModal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
}

.active {
  display: flex;
  justify-content: center;
  align-items: center;
}

.myModalContent {
  padding: 40px 15px;
  min-width: 250px;
  background-color: white;
}